<template>
  <div class='searchtop'>
      <div class='searchbox'>
        <div class="container-fluid">
        <div class='row'>
            <div class="col-lg-4">
                <div class="form-group">
                <label class="col-lg-4 control-label">发布时间</label>
                <div class="col-lg-8">
                    <input type="date" class=" form-control" v-model='lefttime' @input='getlefttime'>
                </div>
            </div>
            </div>
            <div class="col-lg-4">
                <div class="form-group">
                <div class="col-lg-4 control-label">
                    <div class='line'></div>
                </div>
            <div class="col-lg-8">
                <input type="date" class='form-control' v-model='righttime'></div>
            </div>
            </div>
            <div class="col-lg-4">
                <div class="form-group">
                <label class="col-lg-4 control-label">类&nbsp;&nbsp;&nbsp;型</label>
                <div class="col-lg-8">
                    <select type="text" class="form-control" v-model="selected">
                        <option v-for="option in options" :key="option.id">{{option.type}}</option>
                    </select>
                </div>
                </div>
            </div>        
        </div>
        <div class='row' style="padding-top: 2rem">
            <div class="col-lg-4">
                <div class="form-group">
                <label class="col-lg-4 control-label">状&nbsp;&nbsp;&nbsp;态</label>
                <div class="col-lg-8">
                    <select type="text" class="form-control" v-model="chooseStatus">
                        <option  v-for="status in Status" :key="status.id">{{status.type}}</option>
                    </select>
                </div>
            </div>
        </div>
        </div>
        <div class='row' style="padding-top: 2rem">
            <div class="col-lg-12">
                <div class="form-group col-lg-offset-10">
                <label class="control-label" >
                    <button class='btn btn-danger' @click='clear'>清空</button>
                    <button class='btn btn-success' @click='search'>搜索</button>
                </label>
            </div>
        </div>
        </div>
        </div>
    </div>     
  </div>
</template>

<script>
export default {
  props: ["size", "page"],
  data() {
    return {
      options: [
        { id: 4, type: "全部" },
        { id: 0, type: "首页banner" },
        { id: 1, type: "找职业banner" },
        { id: 2, type: "找精英banner" },
        { id: 3, type: "行业大图" }
      ],
      Status: [
        { id: "", type: "全部" },
        { id: 2, type: "上线" },
        { id: 1, type: "草稿" }
      ],
      lefttime: "",
      righttime: "",
      selected: "全部",
      chooseStatus: "全部"
    };
  },
  methods: {
    changeStatus(x) {
      if (x === "全部") {
        return "";
      } else if (x === "草稿") {
        return 1;
      } else if (x === "上线") {
        return 2;
      }
    },
    select(x) {
      if (x === "全部") {
        return "";
      } else if (x === "首页banner") {
        return "0";
      } else if (x === "找职业banner") {
        return 1;
      } else if (x === "找精英banner") {
        return 2;
      } else if (x === "行业大图") {
        return 3;
      }
    },
    getlefttime(x) {
      return new Date(x).getTime() - 28800000;
    },
    getrighttime(x) {
      return new Date(x).getTime() + 57599999;
    },
    search() {
      let select = this.select(this.selected) || undefined;
      let lefttime = this.getlefttime(this.lefttime) || undefined;
      let righttime = this.getrighttime(this.righttime) || undefined;
      let status = this.changeStatus(this.chooseStatus) || undefined;
      this.$router.push({
        path: "/backstage/article/1/10",
        query: {
          startAt: lefttime,
          endAt: righttime,
          type: select,
          status: status
        }
      });
    },
    clear() {
      this.lefttime = "";
      this.righttime = "";
      this.selected = "全部";
      this.chooseStatus = "全部";
      this.$router.push({ path: "/backstage/article/1/10" });
    }
  }
};
</script>

<style scoped>
.searchtop {
  margin: 3rem;
  border: 1px solid #ddd;
  border-radius: 10px;
}
.searchbox {
  padding-top: 2rem;
  padding-left: 4rem;
  background: #fff;
  border-radius: 10px;
}
label {
  margin-top: 7px;
}
.row div {
  text-align: left;
}
.line {
  border-top: 2px solid;
  width: 20%;
  position: absolute;
  transform: translateY(-50%);
  top: 16px;
  height: 0;
}
.col-lg-1 {
  width: 10%;
}
</style>



