<template>
<div>
    <div @keyup.enter='login'>
        <username @name='name' v-model='username'></username>
        <password @pwd='word' v-model="password"></password>
        <button @click='login'>Login</button>
    </div>
</div>
</template>

<script>
import username from "@/components/username.vue";
import password from "@/components/password.vue";
import Ajaxservice from "@/ajax/ajax-service";
export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    name(m) {
      this.username = m.name;
    },
    word(n) {
      this.password = n;
    },
    login() {
      let data = new URLSearchParams({
        name: this.username,
        pwd: this.password
      });
      Ajaxservice.login(data).then(res => {
        res.data.code === 0
          ? this.$router.push("/backstage")
          : alert(res.data.message);
      });
    }
  },
  components: {
    username,
    password
  }
};
</script>

<style>

</style>



