import axios from 'axios'
import pathProject from '@/ajax/AJAX'



export default {
  login: function (data) {
    return axios({
      method: 'post',
      url: '/api' + pathProject.login(),
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      data: data,
    })
  },
  article: function (a, b, c, d,e,f) {
    return axios.get(`/api${pathProject.getArticleList()}?startAt=${a}&endAt=${b}&status=${c}&type=${d}&page=${e}&size=${f}`)
  },
  status: function (x, y) {
    return axios.put(`/api${pathProject.changestatus()}?id=${x}&status=${y}`)
  },
  delete: function (id) {
    return axios.delete(`/api${pathProject.delete(id)}`)
  },
  imgupload: function (data) {
    return axios.post(`/api${pathProject.upload()}`, data)
  },
  add: (data) => {
    return axios.post(`/api${pathProject.add()}`, data)
  },
  edit: id => {
    return axios.put(`/api${pathProject.edit(id)}`)
  },
  single:id=>{
      return axios.get(`/api${pathProject.single(id)}`)
  }
}



