<template>
    <div>
        <input type="password" autocomplete="new-password" v-model='password' @input='pwd'>
    </div>
</template>

<script>
export default {
  data() {
    return {
      password: ""
    };
  },
  methods: {
    pwd() {
      this.$emit("pwd", this.password);
    }
  }
};
</script>

<style>

</style>



