<template>
  <div id="page-wrapper" class="page-wrapper">
     <div class="panel panel-default">
    <div class="panel-heading">
        <strong v-if="!query.id">新增Article</strong>
        <strong v-else>编辑Article</strong>        
    </div>
    <div class="panel-body">
        <form class="form-horizontal" name="myform">
            <div class="form-group">
                <label class="col-md-2 control-label">标题名称</label>
                <div class="col-md-8">
                    <input class="form-control" type="text" v-model='title'>
                </div>
            </div>
            <div class="form-group">
                <label class="col-md-2 control-label word-spacing-25">类 型</label>
                <div class="col-md-2">
                    <select class="form-control" v-model="type1">
                        <option value="" disabled>请选择</option>
                        <option value="0">首页Banner</option>
                        <option value="1">找职位Banner</option>
                        <option value="2">找精英Banner</option>
                        <option value="3">行业大图</option>
                    </select>
                </div>
                <div class="col-md-2" v-if="type1 == 3">
                    <select class="form-control" v-model="type2">
                        <option value="0">移动互联网</option>
                        <option value="1">电子商务</option>
                        <option value="2">企业服务</option>
                        <option value="3">O2O</option>
                        <option value="4">教育</option>
                        <option value="5">金融</option>
                        <option value="6">游戏</option>
                    </select>
                </div>
            </div>
            <div class="form-group">
                <label class="col-md-2 control-label word-spacing-25">说 明</label>
                <div class="col-md-8">
                    <input class="form-control" type="text" v-model="explain">
                </div>
            </div>
            <div class="form-group">
                <label class="col-md-2 control-label">跳转链接</label>
                <div class="col-md-8">
                    <input class="form-control" type="url" v-model="urlLink">
                </div>
            </div>
            <upload @load="load" @deleteUrl='deleteUrl' :imgurl='imgurl'></upload>
            <div class="form-group">
                <div class="col-md-2"></div>
                <div class="col-md-8">
                    <button type="button" class="btn btn-success" @click='online(2)'>立即上线
                    </button>
                    <button type="button" class="btn btn-success" @click='online(1)'>存为草稿
                    </button>
                    <button type="button" class="btn btn-default pull-right" @click='cancel'>取消</button>
                </div>
            </div>
        </form>
    </div>
</div>
  </div>

</template>

<script>
import upload from "@/components/fileupload";
import Ajaxservice from "@/ajax/ajax-service";
export default {
  data() {
    return {
      type1: "",
      type2: 0,
      title: "",
      explain: "",
      urlLink: "",
      imgurl: "",
      query: {}
    };
  },
  created() {
    this.query = this.$route.query;
    if (this.query.id) {
      Ajaxservice.single(this.query.id)
        .then(res => {
          if (res.data.code === 0) {
            let article = res.data.data.article;
            this.title = article.title;
            this.type1 = article.type;
            this.explain = article.content;
            this.urlLink = article.url;
            this.imgurl = article.img;
            this.type2 = article.industry;
          }
        })
        .catch(res => {
          console.log(res);
        });
    }
  },
  methods: {
    load(x) {
      console.log(`接收到的参数为${x}`);
      this.imgurl = x;
    },
    deleteUrl() {
      this.imgurl = "";
    },
    online(x) {
      this.type1 !== 3 ? (this.type2 = "") : (this.type2 = Number(this.type2));
      let data = {
        title: this.title,
        type: Number(this.type1),
        status: x,
        img: this.imgurl,
        content: this.explain,
        url: this.urlLink,
        industry: this.type2
      };
      let senddata = new URLSearchParams(data);
      Ajaxservice.add(senddata)
        .then(res => {
          if (res.data.code === 0) {
            this.$router.go(-1);
          }
        })
        .catch(res => {
          console.log(res);
        });
    },
    cancel() {
      this.$router.go(-1);
    }
  },
  components: {
    upload
  }
};
</script>

<style>

</style>



