<template>
  <div class='searchtop'>
      <div class='searchbox'>
            <div style="overflow:hidden">
              <span class='article-title'>Article列表</span>
              <button class="btn btn-success add" @click='add'>+新增</button>
            </div>
            <table class="table table-striped" style="border:1px solid #eee">
                <tr>
                <td>ID</td>
                <td>名称</td>
                <td>类型</td>
                <td>发布时间</td>
                <td>修改时间</td>
                <td>发布者</td>
                <td>状态</td>
                <td>操作</td>
                </tr>
                <tbody>
                <tr v-for='(article,index) in lists' :key='article.id'>
                <td>{{index+1}}</td>
                <td>{{article.title}}</td>
                <td>{{article.type | type}}</td>
                <td>{{article.createAt | formatDate}}</td>
                <td>{{article.updateAt | formatDate}}</td>
                <td>{{article.author}}</td>
                <td>{{article.status | status}}</td>
                <td><span  @click='changestatus(article.id,article.status)'>{{article.status | status2}}</span>
                <span @click='edit(article.id)'>编辑</span>
                <span @click="Delete(article.id)">删除</span>
                </td>
                </tr>
                </tbody>  
            </table>
      </div>
      <page :total='total' :page='page' :size='size'></page>
  </div>
</template>

<script>
import Ajaxservice from "@/ajax/ajax-service";
import { formatDate } from "@/components/formdate";
import page from "@/components/page";
export default {
  props: ["size", "page"],
  data() {
    return {
      lists: [],
      total: ""
    };
  },
  components: {
    page
  },
  methods: {
    add() {
      this.$router.push("/backstage/articleDetail");
    },
    edit(x) {
      this.$router.push({ path: "/backstage/articleDetail", query: { id: x } });
    },
    first() {
      let startAt = this.$route.query.startAt || "";
      let endAt = this.$route.query.endAt || "";
      let status = this.$route.query.status || "";
      let type = this.$route.query.type || "";
      let page = this.$route.params.page || 1;
      let size = this.$route.params.size || 10;
      Ajaxservice.article(startAt, endAt, status, type, page, size)
        .then(res => {
          if (res.data.code === 0) {
            this.lists = res.data.data.articleList;
            this.total = res.data.data.total;
          }
        })
        .catch(res => {
          console.log(res);
        });
    },
    changestatus(x, y) {
      y === 1 ? (y = 2) : (y = 1);
      Ajaxservice.status(x, y).then(res => {
        console.log(res);
        if (res.data.code === 0) {
          this.$router.go(0);
        }
      });
    },
    Delete(id) {
      Ajaxservice.delete(id).then(res => {
        console.log(res);
        if (res.data.code === 0) {
          this.$router.go(0);
        }
      });
    }
  },
  watch: {
    $route(newVal, oldVal) {
      if (newVal) {
        this.first();
      }
    }
  },
  created() {
    this.first();
  },
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatDate(date, "yyyy-MM-dd hh:mm:ss");
    },
    type(x) {
      if (x === 0) {
        return "首页banner";
      } else if (x === 1) {
        return "找职业banner";
      } else if (x === 2) {
        return "找精英banner";
      } else if (x === 3) {
        return "行业大图";
      }
    },
    status(x) {
      if (x === 1) {
        return "草稿";
      } else if (x === 2) {
        return "上线";
      }
    },
    status2(x) {
      if (x === 1) {
        return "上线";
      } else if (x === 2) {
        return "下线";
      }
    }
  }
};
</script>

<style scoped>
.searchtop {
  margin: 3rem;
  border: 1px solid #ddd;
  border-radius: 10px;
}
.searchbox {
  padding-left: 4rem;
  background: #fff;
  border-radius: 10px;
}
.article-title {
  float: left;
  font-weight: 900;
  padding-top: 14px;
}
.add {
  float: right;
}
.btn {
  margin: 1rem 5rem;
  padding: 3px;
}
td {
  vertical-align: middle !important;
  max-width: 180px;
  word-wrap: break-word;
  word-break: break-all;
  cursor: pointer;
}
span {
  cursor: pointer;
}
</style>


