<template>
    <div>
        <input type="text" autofocus='autofocus' v-model='username' @input="send">
    </div>
</template>

<script>
export default {
  data() {
    return {
      username: ""
    };
  },
  methods: {
    send() {
      this.$emit("name", { name: this.username });
    }
  }
};
</script>

<style>

</style>


