<template>
    <div class='leftNav'>
      <section>
            <ol @click='select(0)' :class="{active:isactive[0]}">
                <li>
                <span class="glyphicon glyphicon-pencil"></span>
                <span  class='info'>Artilce管理</span>
                <span class="glyphicon glyphicon-chevron-left" 
                :class="[this.rotate[0]?'rotate':'cover']"></span>
                </li>
            </ol>
            <ol :class="[{show:rotate[0]}]" class="basic">
                <li @click='articleManager'>Article管理</li>
                <li>文章管理</li>
            </ol>
        </section>
        <section>
            <ol @click='select(1)' :class="{active:isactive[1]}">
                <li>
                <span class="glyphicon glyphicon-pencil"></span>
                <span class='info'>信息管理</span>
                <span class="glyphicon glyphicon-chevron-left" 
                :class="[rotate[1]?'rotate':'cover']"></span>
                </li>
            </ol>        
            <ol :class="[{show:rotate[1]}]" class="basic">
                <li  @click='company'>公司管理</li>
                <li @click='job'>职位列表</li>
            </ol>            
        </section>
        
        <section>
            <ol @click='select(2)' :class="{active:isactive[2]}">
                <li>
                <span class="glyphicon glyphicon-pencil"></span>
                <span  class='info'>用户管理</span>
                <span class="glyphicon glyphicon-chevron-left" 
                :class="[this.rotate[2]?'rotate':'cover']"></span>
                </li>
            </ol>
            <ol :class="[{show:rotate[2]}]" class="basic">
                <li>Article管理</li>
                <li>文章管理</li>
            </ol>
        </section>
        <section>
            <ol @click='select(3)' :class="{active:isactive[3]}">
                <span class="glyphicon glyphicon-pencil"></span>
                <span  class='info'>文章管理</span>
                <span class="glyphicon glyphicon-chevron-left" 
                :class="[this.rotate[3]?'rotate':'cover']"></span>
            </ol>
            <ol :class="[{show:rotate[3]}]" class="basic">
                <li>Article管理</li>
                <li>文章管理</li>
            </ol>
        </section>
    </div>
</template>

<script>
export default {
  data() {
    return {
      rotate: {
        0: false,
        1: false,
        2: false,
        3: false
      },
      isactive: {
        0: false,
        1: false,
        2: false,
        3: false
      }
    };
  },
  methods: {
    select(x) {
      this.isactive = {
        0: false,
        1: false,
        2: false,
        3: false
      };
      this.isactive[x] = true;
      this.rotate[x] = !this.rotate[x];
      for (let i = 0; i < Object.keys(this.rotate).length; i++) {
        if (i != x) {
          this.rotate[i] = false;
        }
      }
    },
    articleManager(){
      console.log(1);
      this.$router.push('/backstage/article/1/10')
    },
    company(){
      this.$router.push('/backstage/company/1/10')
    },
    job(){
      this.$router.push('/backstage/job/1/10')
    }
  }
};
</script>

<style scoped>
.leftNav {
  min-height: 100vh;
  background: url('../assets/login-bg-orange.jpg') #f7a051 no-repeat center;
  width: 20%;
  /* height: calc(200vh - 5rem); */
  padding-top: 3rem;
  float: left;
  padding-bottom:3000px;
  margin-bottom:-3000px
}
ol {
  cursor: pointer;
  margin-left: -3rem;
  padding: 0.5rem 0 10px;
  color: #fff;
  font-size: 1.5rem;
  margin-bottom: 0;
}
ol li {
  cursor: pointer;
}
.info {
  display: inline-block;
  width: 10rem;
  text-align: left;
}
.active {
  background: #feae45;
}
.rotate {
  transform: rotate(-90deg);
  transition: all 0.3s ease;
}
.cover {
  transform: rotate(0deg);
  transition: all 0.3s ease;
}
.basic {
  padding: 0;
  overflow: hidden;
  height: 0;
  transition: height 0.3s ease;
}
.basic li {
    line-height: 1.5rem;
    padding: 1rem 0
}
.show {
  height: 7rem;
}
</style>



