<template>
  <div>
      <nav aria-label="Page navigation">
  <ul class="pagination">
       <li>
      <a href="#" aria-label="Previous" @click='firstpage'>
        首页
      </a>
    </li>
    <li :disabled="page==='...'||page == $route.params.page" v-for='page in pagebox' :key='page' ><a :disabled="page==='...'||page == $route.params.page" @click='pagego(page)' class='normal' :class='{active:page == $route.params.page}'>{{page}}</a></li>   
   <li>
      <a href="#" aria-label="Previous" @click='lastpage'>
        末页
      </a>
    </li>
  </ul>
</nav>
  </div>
</template>

<script>
export default {
  data() {
    return {
      totallist: this.total,
      Page: Number(this.page),
      Size: Number(this.size),
      pagebox: [],
      pages: "",
      active: false
    };
  },
  props: ["total", "page", "size"],
  methods: {
    pageselect() {
      this.pagebox = [];
      if (this.$route.params.page == 1) {
        if (this.pages > 5) {
          this.pagebox = [1, 2, 3, 4, "...", ">"];
        } else {
          for (let i = 1; i <= this.page.length; i++) {
            this.pagebox.push(i);
          }
        }
      } else if (this.pages - this.$route.params.page < 5) {
        this.pagebox = [
          "<",
          "...",
          Number(this.pages) - 4,
          Number(this.pages) - 3,
          Number(this.pages) - 2,
          Number(this.pages) - 1,
          Number(this.pages),
          ">"
        ];
      } else if (this.$route.params.page != this.pages) {
        this.pagebox = [
          "<",
          Number(this.$route.params.page),
          Number(this.$route.params.page) + 1,
          Number(this.$route.params.page) + 2,
          "...",
          ">"
        ];
      }
    },
    firstpage() {
      this.$router.push({
        path: `/backstage/article/1/10`,
        query: this.$route.query
      });
      this.pageselect();
    },
    lastpage() {
      this.$router.push({
        path: `/backstage/article/${this.pages}/10`,
        query: this.$route.query
      });
      this.pageselect();
    },
    pagenation() {
      this.pagebox = [];
      let total = this.total;
      let page = this.Page;
      let size = this.Size;
      this.pages = Math.ceil(total / size);
      this.pageselect();
    },
    pagego(page) {
      console.log(page);
      if (page == "<") {
        page = Number(this.$route.params.page) - 1;
      } else if (page == ">" && Number(this.$route.params.page) < this.pages) {
        page = Number(this.$route.params.page) + 1;
      } else if (page == ">" && Number(this.$route.params.page) == this.pages) {
        page = Number(this.pages);
      } else if (page == "...") {
        return;
      }
      this.$router.push({
        path: `/backstage/article/${page}/${this.$route.params.size}`,
        query: this.$route.query
      });
      this.pageselect();
    }
  },
  created() {
    this.pagenation();
  },
  watch: {
    total(n, o) {
      if (this.total !== this.totallist) {
        this.pagenation();
      }
    }
  }
};
</script>

<style>
.normal {
  background: #fff;
  color: #337ab7;
  cursor: pointer;
}
.active {
  background: #337ab7 !important;
  color: #fff !important;
}
</style>



