<template>
<div>
    <header>
        <div class='sys sys-left'>管理系统</div>
        <div class='sys sys-right'>
            <span>管理员</span>
            <span>admin</span>
            <span class='glyphicon glyphicon-off'></span>
        </div>
    </header>
    <Nav></Nav>
    <router-view style='width:calc(100vw - 22%);float:left'></router-view>
</div>
</template>

<script>
import Nav from "@/components/left"
import welcome from '@/components/welcome'
export default {
  components: {
    Nav,
  }
};
</script>

<style>
header {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  height: 5rem;
  justify-content: space-between;
}
.sys {
  color: #fff;
  padding: 1.5rem 0;
}
.sys-left {
  font-size: 2rem;
  width: 20%;
  background: #f7a051;
  font-weight: 900;
  border-bottom: 1px solid #fdbb6b;
  line-height: 1;
}
.sys-right {
  line-height: 2rem;
  width: 80%;
  background: #fff;
  color: deepskyblue;
  font-weight: 600;
  border-bottom: 1px solid #fff;
  text-align: right;
  padding-right: 10rem
}
</style>



